* {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  font-size: 112.5%;
  font-family: 'Roboto', sans-serif;
  background: #a8c2f0;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

main {
  margin: 30px;
  overflow: scroll;
  flex: 1 0 auto;
  height: 100%;
}

.light__button {
  background-color: #a387cf;
  display: inline-block;
  padding: 10px 25px 10px 25px;
  margin: auto;
  border-radius: 7px;
  font-size: 80%;
  cursor: pointer;
}

.night__button {
  background-color: #a387cf;
  display: inline-block;
  padding: 10px 25px 10px 25px;
  margin: auto;
  border-radius: 7px;
  font-size: 80%;
  cursor: pointer;
}

.light-mode {
  background: radial-gradient(
    circle,
    rgba(134, 165, 217, 1) 47%,
    rgba(95, 75, 182, 1) 71%
  );
  content: 'Hide answer';
}

.displaynone {
  display: none;
}
